<template>
    <div>
        <a-button type="primary" @click="handleCreate()" style="margin-bottom: 10px">新增</a-button>
        <a-table :columns="columns"
                 rowKey="id"
                 :dataSource="data"
                 :pagination="false">
            <template slot="operation" slot-scope="text, record, index">
                <div class='editable-row-operations'>
                    <a-button type="primary"
                              size="small"
                              @click="handleCreate(record.id)">新增子栏目
                    </a-button>
                    <a-divider type="vertical"/>
                    <a-button type="primary"
                              size="small"
                              @click="handleEdit(record)">编辑
                    </a-button>
                    <a-divider type="vertical"/>
                    <a-popconfirm title="确认要删除吗?"
                                  @confirm="handleDelete(record.id)"
                                  okText="是"
                                  cancelText="否">
                        <a-button type="danger"
                                  size="small">删除
                        </a-button>
                    </a-popconfirm>
                </div>
            </template>
        </a-table>
        <modal ref="modal" @dataInit="dataInit"/>
    </div>
</template>

<script>
    import {getCategoryTreeList, getCategory, deleteCategory} from '@/api/category'
    import modal from './Modal.vue'

    const columns = [
        {
            title: 'id',
            dataIndex: 'id',
            key: 'id',
        },
        {
            title: '分类名称',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: '文章数量',
            dataIndex: 'posts_count',
            key: 'posts_count',
        },
        {
            title: '描述',
            dataIndex: 'description',
            key: 'description',
        },
        {
            title: '操作',
            dataIndex: 'operation',
            scopedSlots: {customRender: 'operation'}
        }
    ];

    export default {
        components: {modal},
        data() {
            return {
                data: [],
                columns,
            }
        },
        methods: {
            dataInit() {
                getCategoryTreeList()
                    .then(res => {
                        this.data = res;
                    })
            },
            handleCreate(parentId = null) {
                this.$refs.modal.show(null, parentId);
            },
            handleEdit(catagory) {
                this.$refs.modal.show(catagory);
            },
            handleDelete(catagoryId) {
                deleteCategory(catagoryId)
                    .then(res => {
                        this.dataInit();
                    });
            },
        },
        created() {
            this.dataInit();
        }
    }
</script>
