<template>
    <div class="clearfix">
        <a-upload
                :action="this.$store.state.common.uploadUrl"
                listType="picture-card"
                :fileList="fileList"
                @preview="handlePreview"
                @change="handleChange">
            <div v-if="fileList.length < length">
                <a-icon type="plus"/>
                <div class="ant-upload-text">{{msg}}</div>
            </div>
        </a-upload>
        <a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel">
            <img alt="example" style="width: 100%" :src="previewImage"/>
        </a-modal>
    </div>
</template>

<script>
    import {getImageUrl} from "@/libs/tools";

    export default {
        props: {
            value: {
                required: false,
                type: [Array],
                default() {
                    return []
                }
            },
            length: {type: Number, default: 1},
            msg: {
                required: false,
                type: String,
                default() {
                    return '点击上传'
                }
            },
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        data() {
            return {
                previewVisible: false,
                previewImage: '',
                fileList: [],
            }
        },
        methods: {
            handleCancel() {
                this.previewVisible = false
            },
            handlePreview(file) {
                let url = file.url ? this.$store.state.common.fileBaseUrl + file.url : null;
                url = url ? url : file.thumbUrl;
                this.previewImage = url;
                this.previewVisible = true
            },
            handleChange({fileList}) {
                this.fileList = fileList;
                let temp = [];
                for (let i = 0; i < this.fileList.length; i++) {
                    if (this.fileList[i].hasOwnProperty('response') && this.fileList[i].response) {
                        this.fileList[i].url = this.fileList[i].response.data;
                    }
                    temp.push(this.fileList[i]);
                    temp[i].thumbUrl = getImageUrl(temp[i].url);
                    temp[i].originFileObj = null;
                    temp[i].lastModifiedDate = null;
                }
                this.$emit('change', temp);
            },
        },
        watch: {
            value(val) {
                if (!val || !val.hasOwnProperty('length')) {
                    return false;
                }
                for (let i = 0; i < val.length; i++) {
                    if (!val[i].hasOwnProperty('thumbUrl') && val[i].url) {
                        val[i].thumbUrl = this.$store.state.common.fileBaseUrl + val[i].url;
                    }
                }
                this.fileList = val;
            }
        }
    }
</script>
<style>
    /* you can make up upload button and sample style by using stylesheets */
    .ant-upload-select-picture-card i {
        font-size: 32px;
        color: #999;
    }

    .ant-upload-select-picture-card .ant-upload-text {
        margin-top: 8px;
        color: #666;
    }
</style>
