<template>
    <a-tree-select
            :style="styleValue"
            :treeData="treeData"
            :value="(value!==null&&value!==0)?value.toString():null"
            @change="onChange"
            searchPlaceholder='请点击选择'
            treeNodeFilterProp='label'
    />
</template>

<script>
    import {TreeSelect} from 'ant-design-vue'
    import {getCategorySelectTreeList} from '@/api/category'

    export default {
        props: {
            value: {
                required: false,
                default() {
                    return ''
                }
            },
            styleValue: {
                required: false,
                default: () => {
                    return {'min-width': '200px'}
                }
            },
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        data() {
            return {
                treeData: [],
            }
        },
        methods: {
            init() {
                getCategorySelectTreeList()
                    .then(res => {
                        this.treeData = res;
                    })
            },
            onChange(value) {
                this.$emit('change', value ? parseInt(value) : 0);
            },
        },
        watch: {},
        created() {
            this.init();
        }
    }
</script>
