<template>
    <div>
        <a-modal
                width="60%"
                :title="title"
                v-model="visible"
                :footer="null"
                :destroyOnClose="true">
            <a-form @submit="handleSubmit"
                    :autoFormCreate="(form)=>{this.form = form}">
                <a-form-item
                        label='父级栏目'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="parent_id"
                >
                    <selectCategory/>
                </a-form-item>
                <a-form-item
                        label='排序'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="sort_order"
                >
                    <a-input/>
                </a-form-item>
                <a-form-item
                        label='名称'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="name"
                        :fieldDecoratorOptions="{rules: [{ required: true,message: '请输入名称' }]}"
                >
                    <a-input/>
                </a-form-item>
                <a-form-item
                        label='描述'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="description"
                >
                    <a-input/>
                </a-form-item>
                <a-form-item
                        label='栏目模板'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="list_template"
                >
                    <a-select
                            placeholder='请选择'
                    >
                        <a-select-option value='list'>list</a-select-option>
                        <a-select-option value='list_online_learning'>list_online_learning</a-select-option>
                        <a-select-option value='list_news'>list_news</a-select-option>
                    </a-select>
                </a-form-item>
                <a-form-item
                        label='文章默认模板'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="article_template"
                >
                    <a-select
                            placeholder='请选择'
                    >
                        <a-select-option value='article'>article</a-select-option>
                        <a-select-option value='article_video_comment'>article_video_comment</a-select-option>
                    </a-select>
                </a-form-item>
                <a-form-item
                        label='缩略图'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="thumbnail"
                >
                    <upload-thumbnail/>
                </a-form-item>
                <a-form-item
                        :wrapperCol="{ span: 12, offset: 5 }"
                >
                    <a-button type='primary' htmlType='submit'>
                        提交
                    </a-button>
                </a-form-item>
            </a-form>
        </a-modal>
    </div>
</template>
<script>
    import {createCategory, updateCategory} from '@/api/category'
    import SelectCategory from '@/components/select/SelectCategory'
    import uploadThumbnail from '@/components/upload/UploadThumbnail'

    export default {
        components: {
            SelectCategory, uploadThumbnail
        },
        data() {
            return {
                visible: false,
                title: '',
                form: null,
                isEdit: false,
                id: null,
                updateUploadThumbnailInitUrl: null,
            }
        },
        methods: {
            show(category = null, parentId = null) {
                if (category) {
                    this.title = '编辑栏目';
                    this.isEdit = true;
                    this.id = category.id;
                    let thumbnailUrl = (category.more && category.more.hasOwnProperty('thumbnail'))
                        ? category.more.thumbnail : '';
                    this.updateUploadThumbnailInitUrl = thumbnailUrl;

                    setTimeout(_ => {
                        this.form.setFieldsValue({
                            parent_id: category.parent_id,
                            sort_order: category.sort_order,
                            name: category.name,
                            description: category.description,
                            list_template: category.list_template,
                            aritcle_template: category.aritcle_template,
                            thumbnail: thumbnailUrl,
                        })
                    }, 500)
                } else {
                    this.title = '新增栏目';
                    this.isEdit = false;
                    this.id = null;
                }

                if (parentId) {
                    setTimeout(_ => {
                        this.form.setFieldsValue({
                            parent_id: parentId,
                        })
                    }, 500)
                }

                this.visible = true
            },
            beforeSubmit() {
                let res = false
                this.form.validateFields(
                    (err) => {
                        if (err) {
                            return false;
                        } else {
                            res = true
                        }
                    })
                return res
            },
            handleSubmit(e) {
                e.preventDefault();

                let request;
                if (this.isEdit) {
                    request = updateCategory(this.id, this.form.getFieldsValue())
                } else {
                    request = createCategory(this.form.getFieldsValue())
                }
                request.then(res => {
                    let msg = this.isEdit ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                    this.$emit('dataInit');
                    this.visible = false;
                })
                return true
            }
        }
    }
</script>
