<template>
    <div>
        <upload-images v-model="list" :length="1" :msg="msg"/>
    </div>
</template>

<script>
    import uploadImages from './UploadImages'
    import {getFileName} from "../../libs/tools";

    export default {
        components: {uploadImages},
        props: {
            value: {
                required: false,
                type: [String],
                default() {
                    return ''
                }
            },
            msg:{
                required:false,
                type:String,
                default(){
                    return '点击上传'
                }
            },
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        data() {
            return {
                list: [],
            }
        },
        watch: {
            value(val) {
                if (val) {
                    this.list = [{
                        uid: -1,
                        name: getFileName(val),
                        url: val,
                    }];
                }
            },
            list(val) {
                let url = val.length ? val[0].url : '';
                this.$emit('change', url);
            }
        }
    }
</script>
