import axios from '@/libs/api.request'
import store from '@/store'

const baseUrl = 'categories/';

export const getCategoryList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

export const getCategoryChildrenAndPublishedArticleList = (id) => {
    let data = {}
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl + id + '/children_and_published_article',
        method: 'get',
        params: data,
    })
}

export const getCategoryTreeList = () => {
    return getCategoryList({current_page: 0, filter: {}}).then(res => {
        let temp = {};
        for (let i = 0; i < res.data.rows.length; i++) {
            temp[res.data.rows[i].id] = res.data.rows[i];
        }

        let level = 10;
        while (level > 0) {
            for (let id in temp) {
                if (temp[id].parent_id === 0) {
                    continue
                }

                if (temp[id].level !== level) {
                    continue
                }

                if (!temp[temp[id].parent_id].hasOwnProperty('children')) {
                    temp[temp[id].parent_id].children = [];
                }

                temp[temp[id].parent_id].children.push(temp[id]);
                delete temp[id];
            }
            level--;
        }

        var result = [];
        for (let id in temp) {
            result.push(temp[id]);
        }

        return result;
    })
}

export const getCategorySelectTreeList = () => {
    return getCategoryList({current_page: 0, filter: {}}).then(res => {
        let temp = {};
        for (let i = 0; i < res.data.rows.length; i++) {
            temp[res.data.rows[i].id] = {
                id: res.data.rows[i].id,
                parent_id: res.data.rows[i].parent_id,
                label: res.data.rows[i].name + ' - 文章 ' + res.data.rows[i].posts_count + ' 篇',
                level: res.data.rows[i].level,
                value: res.data.rows[i].id.toString(),
            };
        }

        let level = 10;
        while (level > 0) {
            for (let id in temp) {
                if (temp[id].parent_id === 0) {
                    continue
                }

                if (temp[id].level !== level) {
                    continue
                }

                if (!temp[temp[id].parent_id].hasOwnProperty('children')) {
                    temp[temp[id].parent_id].children = [];
                }

                temp[temp[id].parent_id].children.push(temp[id]);
                delete temp[id];
            }
            level--;
        }

        var result = [];
        for (let id in temp) {
            result.push(temp[id]);
        }
        return result;
    })
}

export const getCategory = (id) => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'get',
        params: data,
    })
}

export const createCategory = (d) => {
    const data = {
        data: d,
    }

    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl,
        method: 'post',
        data,
        params,
    })
}

export const updateCategory = (id, d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'put',
        data,
        params,
    })
}

export const deleteCategory = (id) => {
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'delete',
        params,
    })
}

